body {
  font-family: sans-serif;
  padding: 1em;
}

.ais-ClearRefinements {
  margin: 1em 0;
}

.ais-SearchBox {
  margin: 1em 0;
}

.ais-Pagination {
  margin-top: 1em;
}

.left-panel {
  float: left;
  width: 200px;
}

.right-panel {
  margin-left: 210px;
}

.ais-InstantSearch {
  max-width: 960px;
  overflow: hidden;
  margin: 0 auto;
}

.ais-Hits-item {
  margin-bottom: 1em;
  width: calc(50% - 1rem);
}

/* This ensures images used in each hit item fill appropriately */
.ais-Hits-item img {
  margin-right: 1em;
  width: 100%;
  height: 100%;
}

.hit-title {
  margin-bottom: 0.5em;
}

.hit-singer {
  color: grey;
  margin-bottom: 0.5em;
}

.hit-info {
  font-size: 90%;
}

.custom-button {
  background-color: #007bff;
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
  margin: 5px;
}

.custom-button:hover {
  background-color: #0056b3;
}

.youtube-icon {
  color: #FF0000; /* YouTube red color */
  font-size: 24px;
  text-decoration: none;
  margin-right: 10px;
}

.youtube-icon:hover {
  color: #cc0000;
}

.genie-icon img {
  display: inline-block;
  width: 24px;
  height: 24px;
}

.video-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.modal-content {
  position: absolute;
  top: 50%;
  left: 50%;
  right: auto;
  bottom: auto;
  margin-right: -50%;
  transform: translate(-50%, -50%);
  background: white;
  border-radius: 10px;
  padding: 20px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
}

/* Add media queries for responsiveness */
@media (max-width: 768px) {
  .left-panel,
  .right-panel {
    float: none;
    width: 100%;
    margin: 0;
  }

  .ais-Hits-item {
    width: 100%;
  }

  .ais-InstantSearch {
    padding: 0 1em;
  }
}

.language-switcher {
  text-align: right;
  margin: 10px;
}

.language-switcher select {
  padding: 5px;
  border-radius: 5px;
  border: 1px solid #ccc;
  background-color: #f9f9f9;
  font-size: 16px;
}

.footer {
  color: grey;
  text-align: center;
  padding: 10px 0;
  font-size: 14px;
  position: relative;
  width: 100%;
}

.footer a {
  color: grey;
  text-decoration: none;
}

.footer a:hover {
  text-decoration: underline;
}

.icon-button {
  background: none;
  border: none;
  cursor: pointer;
  color: inherit;
  padding: 0;       /* Remove default padding */
  margin-left: 10px; 
  font-size: 0.8em;
}

.icon-button:hover {
  color: red;
}
